/*
* Calc styles
 */
@use "sass:math";

.calc {
    $track-width: 90%;
    width: 100%;

    display: flex;

    @media (max-width: 1023px) {
        flex-wrap: wrap;

        margin-top: 16px;
    }

    .calc-group-wrap {
        padding: 16px 24px 24px;
        border-radius: 24px;
        background: linear-gradient(162.19deg, #38b2ff 12.16%, #0f85ff 87.84%),
            radial-gradient(
                45.16% 121.43% at 21.79% 0%,
                #72d9ff 0%,
                rgba(101, 196, 255, 0) 100%
            );
        box-shadow: 0px 4px 25px 0px #0d95ff26;

        @media (max-width: 1023px) {
            padding: 8px 12px 16px;
        }
    }

    &-group {
        color: #fff;

        &[data-field="amount"] {
            .calc-group__notice {
                margin-top: 4px;
            }
        }

        &[data-field="term"] {
            .slider-wrapper {
                &:before {
                    content: "";

                    width: 100%;
                    height: 8px;

                    position: absolute;

                    bottom: 0;

                    border-radius: 6px;
                    background-color: rgba(255, 255, 255, 0.12);
                }
            }

            .input-wrap {
                width: $track-width;
            }

            .dot {
                &_right {
                    right: auto;
                    left: $track-width;
                    margin-left: -14px;

                    &:after {
                        content: "90";

                        margin-left: 15px;

                        opacity: 0.4;
                    }
                }
            }
            .calc-group__value {
                &:before {
                    content: none;

                    font-size: 20px;
                    line-height: 24px;
                    font-weight: 500;
                }

                &:after {
                    content: " days";

                    @media (max-width: 1023px) {
                        font-size: 24px;
                    }
                }
            }
        }

        & + .calc-group {
            margin-top: 26px;
        }

        &__label {
            font-size: 24px;

            @media (max-width: 1023px) {
                font-size: 20px;
            }
        }

        &__value {
            font-size: 48px;
            line-height: 48px;

            @media (max-width: 1023px) {
                font-size: 40px;
            }
        }

        &__notice {
            width: 122px;

            float: right;

            margin-top: 2px;

            font-size: 16px;
            text-align: right;

            @media (max-width: 1023px) {
                font-size: 14px;
            }
        }
    }

    .with-currency {
        &:before {
            content: "₦ ";

            font-size: 40px;
            line-height: 40px;
            font-weight: 500;
        }
    }

    &-total,
    &-submit {
        background: #fff;
        padding-right: 16px;
        padding-left: 16px;
    }

    &-total {
        padding-top: 8px;
        border-radius: 24px 24px 0 0;
    }

    &-submit {
        position: relative;

        border-radius: 0 0 24px 24px;

        padding-bottom: 16px;

        &:before {
            content: "";

            position: absolute;

            right: 0;
            bottom: 0;

            width: 100%;
            height: calc(100% + 40px);

            border-radius: 24px;

            box-shadow: 0px 4px 25px rgba(15, 133, 255, 0.25);

            z-index: -1;
        }

        .calculator-submit {
            width: 100%;

            display: inline-block;

            margin-bottom: 16px;
            padding: 8px;

            border-radius: 8px;
            background: #1a4c6c;

            box-shadow: 0px 4px 25px 0px rgba(15, 133, 255, 0.25);

            text-align: center;
            color: #fff;

            &:hover {
                background: radial-gradient(
                        45.16% 121.43% at 21.79% 0%,
                        #72d9ff 0%,
                        rgba(101, 196, 255, 0) 100%
                    ),
                    linear-gradient(162.19deg, #38b2ff 12.16%, #0f85ff 87.84%);
            }

            span {
                display: inline-block;

                margin-left: 8px;

                font-size: 22px;
                font-weight: 500;

                vertical-align: middle;
            }

            .icon {
                display: inline-block;
                path {
                    fill: #fff;
                }
            }
        }
    }

    .agreement-term {
        display: flex;
        justify-content: space-between;
        align-items: center;

        width: 100%;

        padding: 0 20px 6px;

        font-size: 16px;

        span,
        li {
            font-weight: 700;
            color: #1c5377;
        }

        ul,
        li {
            margin-top: 0;
        }

        ul {
            margin-top: 0;
        }

        li {
            display: inline-block;

            & + li {
                margin-left: 8px;
            }

            &.locked {
                position: relative;

                color: rgba(26, 76, 108, 0.5);
                &:after {
                    content: "";

                    position: absolute;

                    top: 0;
                    right: -4px;

                    width: 8px;
                    height: 10px;

                    background: url("./icon-lock.svg") no-repeat;
                }
            }
        }
    }

    .calc-total {
        display: flex;
        justify-content: space-between;
        align-items: center;

        font-weight: 600;

        &__label {
            font-size: 24px;

            @media (max-width: 1023px) {
                font-size: 20px;
            }
        }

        &__value {
            font-size: 32px;
            line-height: 32px;

            @media (max-width: 1023px) {
                font-size: 24px;
            }

            &.with-currency {
                &:before {
                    @media (max-width: 1023px) {
                        font-size: 24px;
                    }
                }
            }
        }
    }

    &_capacitor {
        // этот блок костылей чтобы поместилась фраза длинная с пильговим периодом.
        .calc-header {
            @media (max-width: 1023px) {
                white-space: normal;
            }
        }

        .control-wrap {
            @media (max-width: 1023px) {
                margin-top: 0;
            }
        }

        .calc-group:last-of-type {
            max-width: 285px;
            @media (max-width: 1023px) {
                max-width: none;
            }
        }

        .calc-actions {
            min-width: 150px;
            max-width: 150px;

            @media (max-width: 1023px) {
                min-width: auto;
                max-width: none;
            }
        }
        // конец костылей
    }
}

.calc-info,
.calc-details__body {
    p,
    h6,
    ul {
        font-size: 10px;
    }

    p,
    ul {
        opacity: 0.75;
    }

    h5 {
        font-size: 12px;
    }

    ul {
        list-style: disc;
        padding-left: 12px;
    }
}

.calc-info {
    p {
        font-size: 11px;
    }
}

.calc-details {
    position: relative;

    margin-top: 18px;
    &__heading {
        display: flex;
        justify-content: space-between;
        align-items: center;

        cursor: pointer;

        &.is-active {
            .icon {
                transform: scaleY(-1);
            }
        }

        span {
            font-size: 12px;
            font-weight: 500;
        }

        .icon_arrow-circle {
            width: 24px;
            height: 24px;

            background-image: url("./arrow-circle.svg");
        }
    }

    &__body {
        position: absolute;

        top: 22px;
        right: -8px;
        left: -8px;

        margin: 0 -8px;
        padding: 16px;

        background-color: #fff;

        box-shadow: 0px 4px 25px rgba(15, 133, 255, 0.25);

        z-index: 20;

        .indented {
            margin-top: 12px;
        }

        h5,
        h6 {
            font-weight: 500;
        }

        h5 {
            margin: 14px 0 10px;
        }

        h6 {
            margin: 12px 0 8px;
        }
    }
}

@mixin rangeTrackShared($height) {
    height: $height;

    border-radius: 0 0 25px 25px;
}

@mixin rangeTrack($height: 8px) {
    input[type="range"] {
        &::-webkit-slider-runnable-track {
            @include rangeTrackShared($height);
        }

        &::-moz-range-track {
            @include rangeTrackShared($height);
        }

        &::-ms-track {
            @include rangeTrackShared($height);
        }
    }
}

@mixin rangeThumbShared($size, $marginTop) {
    width: $size;
    height: $size;

    position: relative;

    border-radius: 50%;

    background-image: url("icon-thumb.svg");
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    background-color: transparent;

    filter: drop-shadow(0px 1px 5px rgba(32, 113, 183, 0.365));

    cursor: pointer;

    z-index: 1;
}

@mixin rangeThumb($size: 32px, $marginTop: 16px) {
    input[type="range"] {
        &::-moz-focus-outer {
            border: 0;
        }

        &::-webkit-slider-thumb {
            -webkit-appearance: none;
            margin-top: -$marginTop;
            border: 0;
            outline: 0;

            @include rangeThumbShared($size, $marginTop);
        }

        &::-moz-range-thumb {
            @include rangeThumbShared($size, $marginTop);

            border: 0;
        }

        &::-ms-thumb {
            width: 42px !important;
            height: 8px !important;

            margin-top: 0;

            border: none;
            border-radius: 4px !important;
        }
    }
}

.slider-wrapper {
    width: 100%;

    position: relative;

    margin-top: math.div(14em, 16);

    z-index: 1;

    @media (max-width: 1023px) {
        order: 4;

        margin-top: math.div(20em, 16);
    }

    .input-wrap {
        position: relative;
    }

    .dot {
        position: relative;

        display: inline-block;

        margin-bottom: 6px;

        font-size: 16px;
        font-weight: 500;

        z-index: -1;

        @media (max-width: 1023px) {
            font-size: 14px;
        }

        &_right {
            position: absolute;

            top: 2px;
            right: 0;
        }
    }

    input[type="range"] {
        display: block;

        width: 100%;
        height: 8px;

        position: relative;

        border-radius: 50px;

        -webkit-appearance: none;

        &:focus {
            outline: none;
        }
    }

    .range__glow {
        position: absolute;
        top: 0;
        left: 0;

        height: 100%;

        background: #fff;

        filter: blur(2.5px);

        z-index: -1;
    }

    @include rangeTrack();
    @include rangeThumb();
}
