/*
Promocode styles
 */
@use "sass:math";

.promocode {
    $input-border-radius: 35px;
    $icon-size: 26px;

    position: relative;

    padding: 16px;

    background-color: #fff;

    z-index: 1;

    &__title {
        margin-bottom: 16px;

        text-align: center;
        font-size: 16px;
        font-weight: 600;

        @media (max-width: 1023px) {
            font-size: 14px;
        }
    }

    &__hint {
        margin-top: 16px;

        font-size: 12px;
        color: #ff7b5d;
    }

    .input-group {
        display: block;

        position: relative;

        > .icon {
            position: absolute;

            top: 50%;
            left: 16px;

            transform: translateY(-50%);

            z-index: 5;
        }
    }

    .form__control {
        width: 100%;

        padding: math.div(8em, 18) math.div(16em, 18) math.div(8em, 18)
            math.div(48em, 18);

        border-radius: 8px;
        background: #fff;
        box-shadow: 0px 4px 25px 0px rgba(13, 149, 255, 0.15);
        border: 2px solid #fff;

        font-size: math.div(18em, 16);
        transition: all 0.3s ease-in-out;

        &::placeholder {
            color: rgba(26, 76, 108, 0.34);
        }

        &:-ms-input-placeholder {
            color: rgba(26, 76, 108, 0.34);
        }

        &::-ms-input-placeholder {
            color: rgba(26, 76, 108, 0.34);
        }

        &:focus {
            box-shadow: none;
            outline: none;
            border-color: #1e282d;
        }
    }
}
