/*
Typing spinner styles
 */

@keyframes loader-circle-2 {
    0%,
    50%,
    100% {
        opacity: 0.2;
    }
    25% {
        opacity: 0.6;
    }
}

.loader-circle-2 {
    position: relative;
    width: 15px;
    height: 15px;

    > div {
        position: absolute;
        background-color: #3c9cf4;
        border-radius: 50%;
        height: 3px;
        width: 3px;
        animation: loader-circle-2 0.8s ease infinite;

        &:nth-child(1) {
            top: 0;
            left: 5px;
        }
        &:nth-child(2) {
            top: 1.5px;
            left: 8.5px;
            animation-delay: 0.1s;
        }
        &:nth-child(3) {
            top: 5px;
            left: 10px;
            animation-delay: 0.2s;
        }
        &:nth-child(4) {
            top: 8.5px;
            left: 8.5px;
            animation-delay: 0.3s;
        }
        &:nth-child(5) {
            top: 10px;
            left: 5px;
            animation-delay: 0.4s;
        }
        &:nth-child(6) {
            top: 8.5px;
            left: 1.5px;
            animation-delay: 0.5s;
        }
        &:nth-child(7) {
            top: 5px;
            left: 0;
            animation-delay: 0.6s;
        }
        &:nth-child(8) {
            top: 1.5px;
            left: 1.5px;
            animation-delay: 0.7s;
        }
    }
}
