.list {
    width: 100%;

    @media (max-width: 1023px) {
        display: flex;
        flex-wrap: wrap;

        max-width: 340px;

        margin: 0 auto;
    }

    &__item {
        display: flex;

        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;

        width: 304px;
        min-height: 238px;

        box-shadow: 0px 4px 16px 0px #0d95ff26;

        border-radius: 16px;

        margin: 10px 0;
        padding: 16px;

        @media (max-width: 1023px) {
            width: 288px;
            padding: 16px;
        }

        &:nth-child(2n) {
            @media (max-width: 1023px) {
                margin-left: auto;
            }
        }

        &:last-child {
            @media (max-width: 1023px) {
                margin: 10px auto;
            }
        }

        p {
            font-size: 14px;

            span {
                & + span {
                    margin-top: 8px;
                }
            }
        }

        span {
            display: flex;
        }
    }
}

.loader-circle-2 {
    margin: 0 auto;
}

.alice-carousel {
    width: 100%;
    max-width: 1000px;

    margin-top: -24px !important;

    @media (min-width: 768px) and (max-width: 1023px) {
        max-width: 700px;
    }

    @media (max-width: 767px) {
        max-width: 340px;
        margin-top: -40px !important;
    }

    @media (max-width: 400px) {
        max-width: 300px;
    }

    &__wrapper {
        height: 330px !important;
        padding: 30px 0 30px 20px !important;

        @media (max-width: 1023px) {
            height: 300px !important;
            padding-left: 0 !important;
        }
    }

    &__prev-btn,
    &__next-btn {
        width: auto !important;

        position: absolute;

        top: 35%;

        font-weight: 600;
        font-size: 26px;

        color: #1e282d;

        @media (max-width: 767px) {
            top: 40%;
        }
    }

    &__prev-btn {
        left: -6%;

        text-align: left;

        @media (max-width: 767px) {
            left: -10%;
        }
    }

    &__next-btn {
        right: -6%;

        text-align: right;

        @media (max-width: 767px) {
            right: -10%;
        }
    }

    &__prev-btn-item,
    &__next-btn-item {
        opacity: 0.8;

        &:hover {
            opacity: 1;

            span {
                color: #1e282d;
            }
        }
    }
}
