.gallery {
    @apply relative h-[870px] mobile:h-[444px] w-full mobile:-mt-10;

    .controls {
        @apply flex justify-between absolute top-[290px] mobile:top-[116px] w-full;

        button {
            @apply first:transform first:-rotate-180 mobile:w-6 first:-ml-1 last:-mr-1;
        }

        svg {
            @apply mobile:w-6;
        }
    }

    img {
        @apply absolute top-0 mobile:right-0 mobile:left-0 mobile:mx-auto opacity-0 transition-opacity ease-in-out duration-300 z-50;
        will-change: opacity;
    }
}

.galleryItemsWrap {
    @apply w-[919px] mobile:w-full mobile:h-full mx-auto;
}

.galleryItem {
    @apply mobile:w-full;
    &.active {
        img {
            @apply opacity-100;
        }
    }
}
